.Drawman {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  text-align: center;

  /* height: 100vw; */
}

.Drawman-img {
  height: 300px;
  border: 5px solid #ccc;
  box-shadow: 2px 5px 15px #333;
}

.Drawman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Drawman-btns {
  width: 40vw;
}
button {
  transition: 350ms ease-in-out;
}
.Drawman-btns button {
  font-weight: 900;
  border: 1px solid #aaa;
  height: 30px;
  width: 30px;
  margin: 5px;
  background: #00bcd4;
  color: #fff;
  box-shadow: 0 6px 2px #0097a7;
  border-radius: 5px;
  /* box-shadow: 2px 2px 5px #333; */
}
.Drawman-btns button:hover {
  background-color: #0097a7;
  box-shadow: 0 6px 12px #0097a7;
  transform: scale(1.2);
}
.Drawman-btns button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 6px #9e9e9e;
}
@media (max-width: 500px) {
  .Drawman-btns {
    width: 90vw;
  }
}
.Drawman-reset {
  background: #00bcd4;
  box-shadow: 0 6px 2px #0097a7;
  color: #eee;
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin-bottom: 40px;
}
.Drawman-reset:hover {
  background-color: #0097a7;
  transform: scale(1.1);
  box-shadow: 0 6px 12px #0097a7;
}
